	var wxCharts = require("../../../utils/wxcharts.js");
	var _self;
	var Charts;
	
	var Data = {
		categories: ['2019/1', '2019/2', '2019/3','2019/4', '2019/5', '2019/6'],
		series: [{
				name: 'AQI',
				data: [363, 370, 360,396, 423, 418]
			}
		]
	};
	var width;
	export default {
		onLoad: function() {
			uni.setNavigationBarTitle({
				title: this.getNowFormatYear()+'市空气质量'
			});
			
			uni.getSystemInfo({
				success(res) {
					width = res.screenWidth - 10;
				}
			})
		},
		data() {
			return {
				 sdate: this.getNowFormatYear(),
	             array: ['2017','2018', '2019', '2020'],
				 index: 2,
			}
		},
		onReady: function() {
			this.ShowCharts("charts", Data);
			//this.hideLoading();
		},
		methods: {
			bindPickerChange: function(e) {
            console.log('picker发送选择改变，携带值为', e.target.value)
            this.index = e.target.value
			
			uni.setNavigationBarTitle({
				title: this.array[this.index]+'市空气质量'
			});
			
            },
			/*显示图表*/
			ShowCharts: function(canvasId, data) {
				Charts = new wxCharts({
					canvasId: canvasId,
					type: 'line',
					legend: true,
					fontSize: 11,
					background: '#FFFFFF',
					animation: true,
					categories: data.categories,
					series: data.series,
					width: width,
					height: 280,
					pixelRatio:1,
				});
			},
			
			goDetail:function(id,storeName){			
				let detail = {
						id: id,
						storeName:storeName,
						date:this.array[this.index]
					}
					uni.navigateTo({
						url: "daydata32?detail=" + encodeURIComponent(JSON.stringify(detail))
					})
			},
			
			getNowFormatYear: function() {
				var date = new Date();
				var seperator1 = "-";
				var year = date.getFullYear();
				var currentdate = year ;
				return currentdate;
			},
		}
	}
